import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {MyserviceService} from '../myservice.service'

export interface PeriodicElement {
    SkillId: number,//202020,
    SkillName: string,//"java",
    Description: string,//"xyz",
    SubscriberId: number,//31,
    ApprovalFlag: boolean, //true,
    ClientSpecificFlg: boolean,// true,
    CreatedBy: string,//"kumar",
    CreatedDate: string,//"2021-03-26",
    ModifiedBy: string//"isravel",
    ModifiedDate: string,//"2021-03-19",
    ActiveFlag: boolean//true
}

@Component({
  selector: 'ngx-jobskills-list',
  templateUrl: './jobskills-list.component.html',
  styleUrls: ['./jobskills-list.component.scss']
})
export class JobskillsListComponent implements OnInit {
  static jobskillsdata;
  displayedColumns: string[] = ['skill', 'name', 'description', 'status', 'action'];
  dataSource;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}
  ngOnInit() {
      this.myservice.getData('skill').subscribe((data) => {
      JobskillsListComponent.jobskillsdata = Array.from(Object.keys(data), k=>data[k]);
      JobskillsListComponent.jobskillsdata=JobskillsListComponent.jobskillsdata
      const ELEMENT_DATA: PeriodicElement[] =JobskillsListComponent.jobskillsdata
      this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);   
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
  });
}
}
