import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
//written on 3/30/2021

import {MyserviceService} from '../myservice.service'


export interface PeriodicElement {
  plan_id: string;
  plan_type: string;
  name: string;
  prod_name: string;
  effective_date: string;
  per_usage: string;
  days: string;
  description: string;
  status: boolean;
}

@Component({
  selector: 'ngx-jobplan-list',
  templateUrl: './jobplan-list.component.html',
  styleUrls: ['./jobplan-list.component.scss']
})
export class JobplanListComponent implements OnInit {

  static jobplan = [];  
  displayedColumns: string[] = ['plan_id', 'plan_type', 'name', 'prod_name', 'effective_date',  'per_usage', 'days', 'description', 'status', 'action'];
    
  dataSource;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}
  ngOnInit() {
    this.myservice.getData('plan').subscribe((data) => {
      JobplanListComponent.jobplan = Array.from(Object.keys(data), k=>data[k]);
      JobplanListComponent.jobplan=JobplanListComponent.jobplan
      const ELEMENT_DATA: PeriodicElement[] =JobplanListComponent.jobplan
      this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);             
      debugger;
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
   });       

  }

  

}
