import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
//written on 3/30/2021

import {MyserviceService} from '../myservice.service'

export interface PeriodicElement {
    JobResCatCode:string// "JC00000001",
    JobResTtlCode:string// "JT00000001",
    JobResposibleCode:string// "JRS0000009",
    JobResponsibleDescription:string// "Forecast and analyze costs of processes, labor and inventory",
    JobResCreatedBy:string// "Patnaik",
    JobResCreatedDate:string// "2021-03-29",
    JobResUpdatedBy:string// "Subramanian",
    JobResUpdatedDate:string// "2021-03-29",
    JobResDelFlag:boolean// false
}



@Component({
  selector: 'ngx-jobresponsibility-list',
  templateUrl: './jobresponsibility-list.component.html',
  styleUrls: ['./jobresponsibility-list.component.scss']
})
export class JobresponsibilityListComponent implements OnInit {
  static jobresponsiblitydata;
  displayedColumns: string[] = ['res_id', 'name', 'description', 'title', 'status', 'action'];
  dataSource;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}

  ngOnInit() {
      this.myservice.getData('jobresponsibility').subscribe((data) => {
      JobresponsibilityListComponent.jobresponsiblitydata = Array.from(Object.keys(data), k=>data[k]);
      JobresponsibilityListComponent.jobresponsiblitydata=JobresponsibilityListComponent.jobresponsiblitydata
      const ELEMENT_DATA: PeriodicElement[] =JobresponsibilityListComponent.jobresponsiblitydata
      this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);   
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
  });
}
}

