import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JobdetailsComponent } from "./jobdetails.component";
import { JobcategoryListComponent } from "./jobcategory-list/jobcategory-list.component";
import { JobtitleListComponent } from "./jobtitle-list/jobtitle-list.component";
import { JobrequirementListComponent } from "./jobrequirement-list/jobrequirement-list.component";


import { JobresponsibilityListComponent } from "./jobresponsibility-list/jobresponsibility-list.component";
import { JobbriefListComponent } from "./jobbrief-list/jobbrief-list.component";
import { JobqualificationListComponent } from "./jobqualification-list/jobqualification-list.component";
import { JobskillsListComponent } from "./jobskills-list/jobskills-list.component";
import { JobperksListComponent } from "./jobperks-list/jobperks-list.component";
import { JobplanListComponent } from "./jobplan-list/jobplan-list.component";


const routes: Routes = [
  {
    path: "",
    component: JobdetailsComponent,
    children: [
      {
        path: "category",
        component: JobcategoryListComponent,
      } ,
      {
        path: "title",
        component: JobtitleListComponent,
      },
      {
        path: "requirement",
        component: JobrequirementListComponent,
      },
      {
        path: "responsibility",
        component: JobresponsibilityListComponent,
      } ,
      {
        path: "brief",
        component: JobbriefListComponent,
      } ,
      {
        path: "qualification",
        component: JobqualificationListComponent,
      } ,
      {
        path: "skill",
        component: JobskillsListComponent,
      },
      {
        path: "perks",
        component: JobperksListComponent,
      },
      {
        path: "plan",
        component: JobplanListComponent
      }     
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class JobdetailsRoutingModule { }
