import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {MyserviceService} from '../myservice.service'


export interface PeriodicElement {
    JobTtlCategoryCode:string,// "JC00000001",
    JobTttleCode:string,// "JT00000004",
    JobTitleDescription:string,// "Mortgage Loan Officer job description",
    JobTitleCreatedBy: string,//"Patnaik",
    JobTitleCreatedDate:string// "2021-02-01",
    JobTitleUpdatedBy:string,// "Subramanian",
    JobTitleUpdatedDate:string,// "2021-02-05",
    JobTitleDelFlag:boolean// true
}

@Component({
  selector: 'ngx-jobtitle-list',
  templateUrl: './jobtitle-list.component.html',
  styleUrls: ['./jobtitle-list.component.scss']
})
export class JobtitleListComponent implements OnInit {
  static jobtitledata;
  displayedColumns: string[] = ['title', 'name', 'description', 'category', 'status', 'action'];
  dataSource;// = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}
  ngOnInit() {
      this.myservice.getData('jobtitle').subscribe((data) => {
      JobtitleListComponent.jobtitledata = Array.from(Object.keys(data), k=>data[k]);
      JobtitleListComponent.jobtitledata=JobtitleListComponent.jobtitledata
      const ELEMENT_DATA: PeriodicElement[] =JobtitleListComponent.jobtitledata
      this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);   
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
  });
}

  

}
