import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
//written on 3/30/2021

import {MyserviceService} from '../myservice.service'


export interface PeriodicElement {
    JobReqCatCode: string,
    JobReqTtlCode: string,
    JobReqCode: string,
    JobReqDescription: string,
    JobReqCreatedBy: string,
    JobReqCreatedDate: string,
    JobReqUpdatedBy: string,
    JobReqUpdatedDate: string,
}

@Component({
  selector: 'ngx-jobrequirement-list',
  templateUrl: './jobrequirement-list.component.html',
  styleUrls: ['./jobrequirement-list.component.scss']
})

export class JobrequirementListComponent implements OnInit {

  static jobrequirements=[];

  displayedColumns: string[] = ['reqId', 'name', 'description', 'title', 'status', 'action'];

  dataSource;

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}

  ngOnInit() {
    this.myservice.getData('jobrequirement').subscribe((data) => {
      JobrequirementListComponent.jobrequirements = Array.from(Object.keys(data), k=>data[k]);
      JobrequirementListComponent.jobrequirements=JobrequirementListComponent.jobrequirements
      const ELEMENT_DATA: PeriodicElement[] =JobrequirementListComponent.jobrequirements
      this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);   
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      });        
  }
}

