import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';

//written on 3/30/2021
import {MyserviceService} from '../myservice.service'


export interface PeriodicElement {
  JobCatCreatedBy: string,//"Patnaik"  
  JobCatCreatedDate: string,//"2021-03-29"
  JobCatDelFlag: boolean,//true
  JobCatDescription:string,// "Facilities"
  JobCatUpdatedBy:string,// "Subramanian"
  JobCatUpdatedDate:string,// "2021-03-29"
  JobCategoryCode:string// "JC00000020"
}
  
  

@Component({
  selector: 'ngx-jobbrief-list',
  templateUrl: './jobbrief-list.component.html',
  styleUrls: ['./jobbrief-list.component.scss']
})

export class JobbriefListComponent implements OnInit {
  static jobbriefdata=[];
  displayedColumns: string[] = ['brief_id', 'name', 'description', 'title', 'status', 'action'];
  dataSource;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}
  
  ngOnInit() {
    this.myservice.getData("jobbrief").subscribe((data) => {
      JobbriefListComponent.jobbriefdata = Array.from(Object.keys(data), k=>data[k]);
      JobbriefListComponent.jobbriefdata=JobbriefListComponent.jobbriefdata
      const ELEMENT_DATA: PeriodicElement[] =JobbriefListComponent.jobbriefdata
      this.dataSource= new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
      debugger;
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
   });    

  }

  

}


