import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';


//written on 3/30/2021

import {MyserviceService} from '../myservice.service'

export interface PeriodicElement {
  JobCatCreatedBy:string,// "Patnaik"  
  JobCatCreatedDate:string// "2021-03-29"
  JobCatDelFlag: boolean//false
  JobCatDescription: string//"Production"
  JobCatUpdatedBy:string// "Subramanian"
  JobCatUpdatedDate:string// "2021-03-29"
  JobCategoryCode:string// "JC00000011"  
}

@Component({
  selector: 'ngx-jobcategory-list',
  templateUrl: './jobcategory-list.component.html',
  styleUrls: ['./jobcategory-list.component.scss']
})



export class JobcategoryListComponent implements OnInit {
  static jobcategorydata = [];
  displayedColumns: string[] = ['category', 'name', 'description', 'status', 'action'];
  dataSource;



  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}
  ngOnInit() {
    this.myservice.getData('jobcategory').subscribe((data) => {
      JobcategoryListComponent.jobcategorydata = Array.from(Object.keys(data), k=>data[k]);
      JobcategoryListComponent.jobcategorydata =JobcategoryListComponent.jobcategorydata 
      const ELEMENT_DATA: PeriodicElement[] =JobcategoryListComponent.jobcategorydata
      this.dataSource= new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
      debugger;
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;      

   });    
  }
 



  

}

