import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
//written on 3/30/2021

import {MyserviceService} from '../myservice.service'

export interface PeriodicElement {
    PerksId: number//404040,
    PerksName: string//"abcx",
    Description: string//"xcba",
    SubscriberId: number//17,
    ApprovalFlag: boolean//true,
    ClientSpecificFlg: boolean//true,
    CreatedBy: string//"Vijay",
    CreatedDate: string//"2021-03-24",
    ModifiedBy: string//"Subbu",
    ModifiedDate: string//"2021-03-24",
    ActiveFlag:boolean// true
}


@Component({
  selector: 'ngx-jobperks-list',
  templateUrl: './jobperks-list.component.html',
  styleUrls: ['./jobperks-list.component.scss']
})
export class JobperksListComponent implements OnInit {

  static jobperksdata = [];  
  displayedColumns: string[] = ['category', 'name', 'description', 'status', 'action'];
  dataSource;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}
  ngOnInit() {
    this.myservice.getData('perks').subscribe((data) => {
      JobperksListComponent.jobperksdata = Array.from(Object.keys(data), k=>data[k]);
      JobperksListComponent.jobperksdata=JobperksListComponent.jobperksdata
      const ELEMENT_DATA: PeriodicElement[] =JobperksListComponent.jobperksdata      
      this.dataSource= new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
      debugger;
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
   });       
  }

  

}

