import { Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
//written on 3/30/2021

import {MyserviceService} from '../myservice.service'

export interface PeriodicElement {
  ActiveFlag: boolean,//false
  ApprovalFlag: boolean,//true
  ClientSpecificFlg: boolean,//true
  CreatedBy: string,//"kumar"
  CreatedDate: string,//"2021-03-28"
  Description: string,//"xyz"
  ModifiedBy: string,//"isravel"
  ModifiedDate: string,//"2021-03-27"
  QualificationID: number,//202020
  QualificationName: string,//"M.E"
  SubscriberId: number//22  
}
@Component({
  selector: 'ngx-jobqualification-list',
  templateUrl: './jobqualification-list.component.html',
  styleUrls: ['./jobqualification-list.component.scss']
})
export class JobqualificationListComponent implements OnInit {
  static jobqualification=[]
  displayedColumns: string[] = ['category', 'name', 'description', 'status', 'action'];
  
  dataSource;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(private myservice: MyserviceService) {}
  
  ngOnInit() {
    this.myservice.getData('qualification').subscribe((data) => {
      JobqualificationListComponent.jobqualification = Array.from(Object.keys(data), k=>data[k]);

      JobqualificationListComponent.jobqualification=JobqualificationListComponent.jobqualification
      const ELEMENT_DATA: PeriodicElement[] =JobqualificationListComponent.jobqualification

      this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);   

      debugger;
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;   
   });    

    
  }

}


